import React from "react";
import { NavLink } from "react-router-dom";

const Common = (props) => {
  return (
    <section className="container  header d-flex align-items-center">
      <div className="row m-auto">
        <div className="col-md-6 d-flex flex-column justify-content-center">
          <h2 className="heading_title">
            {props.pageInfo}
            <br />
            <strong>SA-Website</strong>
          </h2>
          <h6 className="my-3 text-muted header_desc">
            We are the Team of Talented developer making websites
          </h6>
          <div className="mt-3">
            <NavLink to={props.redirect} className="btn btn-outline-primary">
              {props.btnName}
            </NavLink>
          </div>
        </div>
        <div className="col-md-6 p-4 text-center">
          <img
            src={props.imgPath}
            alt="homepageimage"
            className="img-fluid animated m-auto"
          />
        </div>
      </div>
    </section>
  );
};

export default Common;
