import React from "react";

const Contact = () => {
  const formSubmitHandler = (event) => {};
  return (
    <>
      <div className="container">
        <section className="mb-4">
          <h2 className="h1-responsive font-weight-bold text-center text-primary service_header">
            Contact us
          </h2>
          <p className="text-center w-responsive mx-auto mb-5">
            Do you have any questions? Please do not hesitate to contact us
            directly. Our team will come back to you within a matter of hours to
            help you.
          </p>
          <div className="row">
            <div className="col-md-12 mb-md-0 mb-5">
              <div>
                <div className="row">
                  <div className="col-md-6">
                    <div className="md-form mb-3">
                      <input
                        type="text"
                        id="name"
                        name="name"
                        autoComplete="off"
                        className="form-control"
                        placeholder="Enter Your Name"
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="md-form mb-3">
                      <input
                        autoComplete="off"
                        type="text"
                        id="email"
                        name="email"
                        className="form-control"
                        placeholder="Enter Your E-mail"
                      />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="md-form mb-3">
                      <input
                        autoComplete="off"
                        type="text"
                        id="subject"
                        name="subject"
                        className="form-control"
                        placeholder="Subject"
                      />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="md-form">
                      <textarea
                        type="text"
                        id="message"
                        name="message"
                        rows="2"
                        className="form-control md-textarea"
                        placeholder="Write Message Here"></textarea>
                    </div>
                  </div>
                </div>
              </div>
              <div className="text-center mt-3 text-md-left">
                <button className="btn btn-primary" onClick={formSubmitHandler}>
                  Send
                </button>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default Contact;
