import React from "react";

const Card = (props) => {
  return (
    <div className="card p-2 border-primary">
      <div className="card-header mb-1 text-center bg-transparent border-primary">
        {props.title}
      </div>
      <img src={props.imgPath} className="card-img-top" alt="card-img" />
      <div className="card-body">
        <small className="card-text text-muted mb-3 d-block">
          {props.content}
        </small>
      </div>
      <div className="card-footer text-center bg-transparent border-primary">
        <button className="btn btn-outline-primary">{props.btnName}</button>
      </div>
    </div>
  );
};

export default Card;
